export default {
name: "",
components: {

},
props: {},
data () {
  return {
	  username:'',
	  password:''
    }
  },
  methods: {
     getuser(e){
		this.username=e.detail.value
	 },
	 getpassword(e){
	 	this.password=e.detail.value
	 },
	 login(){
		uni.request({
			url: `${this.$api}/user/registOrLogin?qq=122212489`,
			method: 'POST',
			data: {
				password:this.password,
				username:this.username
			},
			success: res => {
				console.log(res);
				uni.setStorageSync('user',res.data.data)
				uni.switchTab({
					url:"/pages/me/me"
				})
			},
			fail: () => {},
			complete: () => {}
		});
	 }
  },
  mounted () {

  },
  onLoad () {

  },
  filters: {

  },
  computed: {

  },
  watch: {

  },
  directives: {

  }
}
